.client {
  margin-right: 0.5rem;
  float: left;
  position: relative;
}

.client__cloud {
  background: url('../cloudIco/CloudIco.svg') no-repeat top left;
  background-size: 8rem 7rem;
  width: 8rem;
  height: 7rem;
  top: -5.2rem;
  position: absolute;
  z-index: 1;
}

.client__ok {
  background: url('../okIco/OkIco.svg') no-repeat top left;
  background-size: 3rem 3rem;
  width: 3rem;
  height: 3rem;
  border-radius: 100%;
  position: absolute;
  top: -3.8rem;
  left: 2.3rem;
  z-index: 2;
}

.client__ko {
  background: url('../koIco/KoIco.svg') no-repeat top left;
  background-size: 3rem 3rem;
  width: 3rem;
  height: 3rem;
  border-radius: 100%;
  position: absolute;
  top: -3.8rem;
  left: 2.3rem;
  z-index: 2;
}

.client__recipe {
  width: 5rem;
  height: 5rem;
  position: absolute;
  top: -4.8rem;
  left: 1.5rem;
  background-size: 5rem 5rem !important;
  z-index: 2;
}

.client__clock {
  background: var(--border-color);
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 100%;
  top: 0.2rem;
  right: 0.2rem;
  position: absolute;
}

.client__client {
  background: url('../catIco/CatIco.svg') no-repeat top left;
  background-size: 8rem 6rem;
  width: 8rem;
  height: 6rem;
  position: relative;
  z-index: 3;
}
